import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { BehaviorSubject } from 'rxjs';



export const API_URL = AppConstants.acctCloserUrl;
@Injectable({
  providedIn: 'root'
})

export class ChargeMaintenanceService {

  constructor(private http: HttpClient) { }
  private Index = new BehaviorSubject<any>({
    index: '',
  })
  setIndex(index: any) {
    this.Index.next(index);
  }

  getIndex() {
    return this.Index.asObservable();
  }
  onchargeMaintenanceSubmit(maker: string, data) {
    return this.http.post<any>(`${API_URL}/charges/save/${maker}`, data);
  }

  onAuthofChargeMaintenance(maker: string, id: number) {
    return this.http.get<any>(`${API_URL}/charges/verify/${maker}/${id}`)
  }

  gettingChargeMaintenance() {
    return this.http.get<any>(`${API_URL}/charges/getSummary`)
  }
  submittingTheEditedRecord(data, inputBy: string) {
    console.log("in sevice", data);
    return this.http.put<any>(`${API_URL}/charges/modify/${inputBy}`, data)
  }
  sendingIdforchargeMaintenanceEdit(id: number) {
    return this.http.get<any>(`${API_URL}/charges/getById/${id}`)
  }
  onClosingTheRecord(inputBy: string, id: number) {
    return this.http.get<any>(`${API_URL}/charges/close/${inputBy}/${id}`)
  }
  onReopningTheRecord(inputBy: string, id: number) {
    return this.http.get<any>(`${API_URL}/charges/reopen/${inputBy}/${id}`)
  }
  onDeletTheRecord(inputBy: string, id: number) {
    return this.http.delete<any>(`${API_URL}/charges/delete/${inputBy}/${id}`)
  }

}
