import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { ApiService } from 'src/app/shared/services/api.service';
import { ChargeMaintenanceService } from 'src/app/shared/services/charge-maintenance-service.service';
import { RoleService } from 'src/app/shared/services/role.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-charge-maintainence-summary',
  templateUrl: './charge-maintainence-summary.component.html',
  styleUrls: ['./charge-maintainence-summary.component.scss']
})
export class ChargeMaintainenceSummaryComponent implements OnInit {
  isLoading:boolean = false;
  respArray: any[] = [];
  roleCodes = new permissionsLabels();
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  currentUser: string;
  statusArr: String[] = ['Authorized', 'Unauthorized', 'Open', 'Close'];
  constructor( private roleService: RoleService,private apiService: ChargeMaintenanceService,private router:Router,
    private cdr:ChangeDetectorRef,private chargeMaintenanceService: ChargeMaintenanceService) {}

  ngOnInit(): void {
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    setTimeout(() => {
      console.log(this.roleCodes);
    }, 3000);
    this.currentUser = localStorage.getItem("userFromLogin");
  
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength : 5,
      columnDefs: [ { type: 'date', 'targets': [5] } ],
      order: [[5, 'desc']],
      processing: true,
      // lengthMenu: [[5, 10, 20, -1], [5, 10, 20, 30]],
      lengthMenu: [
        [5, 10, 20, 30,-1],
        [5, 10, 20, 30,"ALL"],
      ],
      retrieve:true
    };

    this.getChargeMaintainenceSummary();

  }
  newRolePermissions()
  {

    this.roleService.fetchScreenPermissions('Charge Maintenance');
  }
  getChargeMaintainenceSummary() {
    this.isLoading = true;
    this.apiService.gettingChargeMaintenance().subscribe((dataresp) => {
      
        for (let data of dataresp) {
          data.verifiedStatus = this.statusArr.find((i) =>
            i.startsWith(data.verifiedStatus)
          );
  
          data.recordStatus = this.statusArr.find((i) =>
            i.startsWith(data.recordStatus)
          );
        }
      console.log(dataresp);
      this.respArray = dataresp;
      this.isLoading = false;

      this.dtTrigger.next();
    });
  
  }


 
  new(){
    localStorage.removeItem('idValue');
    this.apiService.setIndex({
      index: "",
    });
    this.router.navigateByUrl("/charge-maintainence/create");
  }
  edit(data){
    this.apiService.setIndex({
      index: data.id,
    });
    localStorage.setItem('idValue',data.id);
    this.router.navigateByUrl("/charge-maintainence/create");
  }

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }
close(data){
  console.log(data);
  
  Swal.fire(

    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to Close record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3e54b8',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
         confirmButtonText: 'YES','icon':'info',iconColor: "#d33",
      
    }
  ).then((result => {
    console.log("this is reopen ",result);
    if(result.isConfirmed=== true){
      console.log("this is close");
  this.chargeMaintenanceService
    .onClosingTheRecord(this.currentUser, data.id)
    .subscribe((closeResp) => {
      console.log(closeResp);
      // this.respData = closeResp;
      // this.auditLog();
      if (closeResp) {
  Swal.fire(
         { text:"Record is Closed"}
          
        );
        this.refreshPage();
        this.ngOnInit();
      
      }
    });
   }}))
}
open(data) {
  Swal.fire(

    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to Reopen record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3e54b8',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
         confirmButtonText: 'YES',
         'icon':'info',iconColor: "#d33"
    }
  ).then((result => {
    console.log("this is reopen ",result);
    if(result.isConfirmed=== true){
    this.chargeMaintenanceService
      .onReopningTheRecord(this.currentUser, data.id)
      .subscribe((reopnResp) => {
        console.log(reopnResp);
     
     
        if (reopnResp.recordStatus == "O") {
       
    Swal.fire({
           text: "Record is Reopened"}
            // type: "success"
          );
          this.refreshPage();
          this.ngOnInit();
      
        } else {
    Swal.fire({text:"Record cannot be opened "});
        }
      });
  }}))
  
}
delete(data) {
  Swal.fire(

    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to Delete record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3e54b8',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
         confirmButtonText: 'YES.','icon':'info',iconColor: "#d33"
    }
  ).then((result => {
    console.log("this is reopen ",result);
    if(result.isConfirmed=== true){
    this.chargeMaintenanceService
    .onDeletTheRecord(this.currentUser, data.id)
    .subscribe((deleteResp) => {
        console.log(deleteResp);
       
     
        if (deleteResp.chargeAmount == null) {
         
    Swal.fire({
           text: "Record is Deleted"}
            // type: "success"
          );
          this.refreshPage();
          this.ngOnInit();
          this.router.navigate(['/charge-maintainence']);
        } else {
    Swal.fire({text:"Record cannot be delete "});
        }
      });
  }}))
  console.log("this is delete");
 
}
auth(data) {
  Swal.fire(

    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to Authorize record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3e54b8',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
         confirmButtonText: 'YES','icon':'info',iconColor: "#d33"
    }
  ).then((result => {
    console.log("this is reopen ",result);
    if(result.isConfirmed=== true){
      if(this.currentUser===data.inputBy){
        Swal.fire({text:'Maker Cannot Authorize Record!',
      icon:'error'});
      return;
      }
      console.log("this is authorzation");
        
      this.chargeMaintenanceService
        .onAuthofChargeMaintenance(this.currentUser, data.id)
        .subscribe((authResp) => {
          console.log(authResp);
      
          if (authResp.verifiedBy != null) {
      Swal.fire(
             {text: "Record is Authorized"}
             
              // type: "success"
            );
            this.refreshPage();
            this.ngOnInit();
          } else {
      Swal.fire(
             { text:"Maker cannot authorize the record"}
            
            );
          }
        });
   }}))

}
check(data){
  console.log(data);
}
refreshPage() {
  window.location.reload();
 }
}
