import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from 'src/app/shared/guards/can-deactivate.guard';
import { ChargeMaintainenceSummaryComponent } from './charge-maintainence-summary/charge-maintainence-summary.component';
import { ChargeMaintaineneceComponent } from './charge-maintainenece.component';


const routes: Routes = [
  {
    path: '',
    component: ChargeMaintainenceSummaryComponent
  },
  {
    path: 'create',
    component: ChargeMaintaineneceComponent,
    canDeactivate: [CanDeactivateGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ChargeMaintaineneceRoutingModule { }
